<template>
	<view class="hc">
		<view class="bg" v-if="navshow">
			<view class="nav" @click="routeAbout()">
				<u-icon size="28" name="/static/img/3258.png" class="footIcon"></u-icon>
			</view>
			<span class="title">日常商品批发</span>
		</view>
		<view class="head">
			<view class="cate-section">
				<view class="cate-item" v-for="(item,index) in navlist" :key="index">
					<image :src="item.src" mode="aspectFit"></image>
					<text>{{item.title}}</text>
				</view>
				<view class="cate-item" @click="navTo('/pages/category/category')">
					<image src="/static/img/temp/44.png" mode="aspectFit"></image>
					<text>全部分类</text>
				</view>
			</view>
			<view class="prolist">
				<v-prolist :plist="list" @eventClick="navTo('/pages/detail/detail?type=2')"></v-prolist>
			</view>
		</view>
	</view>
</template>

<script>
	import proList from '@/components/prolist.vue'
	export default {
		components: {
			"v-prolist":proList
		},
		data() {
			return {
				navshow:false,
				navlist:[
					{
						src:"/static/img/temp/23.png",
						title:"新鲜蔬菜"
					},
					{
						src:"/static/img/temp/40.png",
						title:"时令水果"
					},
					{
						src:"/static/img/temp/24.png",
						title:"肉禽蛋品"
					},
					{
						src:"/static/img/temp/26.png",
						title:"海鲜水产"
					},
					{
						src:"/static/img/temp/68.png",
						title:"酒水饮料"
					},
					{
						src:"/static/img/temp/30.png",
						title:"休闲零食"
					},
					{
						src:"/static/img/temp/31.png",
						title:"粮油调味"
					},
					{
						src:"/static/img/temp/32.png",
						title:"速食冻品"
					},
					{
						src:"/static/img/temp/33.png",
						title:"日用百货"
					}
				],
				list:[]
			}
		},
		methods: {
			routeAbout() {
				this.$u.route({
					type:'switchTab',
					url:'pages/home/home'
				})
			},
			navTo(url){
				uni.navigateTo({
				    url: url
				});
			},
			//请求数据
			async loadData(){
				let list = await this.$api.json('goodsList');
				this.list = list;
			}
		},
		onLoad(){
			this.loadData();
		}
	}
</script>

